.dancing-script {
  font-optical-sizing: auto;
  font-family: Dancing Script, cursive;
  font-style: normal;
  font-weight: 800;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  height: 100vh;
}

.contenedor {
  background: #fff;
  flex-flow: wrap;
  width: 100%;
  margin: auto;
  font-size: 1.5em;
  display: flex;
}

header {
  background-color: #f2f2f2;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 30px 20px;
  display: flex;
}

.nav {
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.nav-links {
  gap: 50px;
  list-style: none;
  display: flex;
}

.nav-links li {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

.nav-links li a {
  color: #41403f;
  transition: color .3;
  text-decoration: none;
}

.nav-links li a:hover {
  color: #b53148;
}

.abrir-menu {
  display: none;
}

.section1 {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.image1 {
  width: 100%;
}

.visita {
  width: 50%;
  font-size: 20px;
  position: absolute;
}

.section2 {
  justify-content: space-between;
  display: flex;
}

.articleSevilla {
  padding: 30px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

.item {
  margin: 20px;
  font-size: larger;
  font-weight: 800;
}

h1 {
  text-align: center;
  color: #fff;
  margin: 50px 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 50pt;
}

.section3 {
  flex-direction: column;
  align-items: center;
  width: 70%;
  margin: 0 auto;
  padding: 20px;
  font-size: 1.2em;
  display: flex;
}

.intro {
  padding-top: 1.5%;
}

.h2Pre {
  padding: 2%;
}

.video {
  justify-content: center;
  margin: 0 auto;
  padding-top: 4%;
  display: flex;
}

.image2 {
  width: 100%;
  margin: 0 auto;
  padding-top: 2%;
  padding-left: 10%;
}

.cat {
  margin: 0 auto;
  padding: 20px;
}

.lista {
  padding: 10%;
}

.pie {
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  padding: .5rem;
  display: flex;
}

.social {
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

@media (width <= 768px) {
  .abrir-menu {
    display: block;
  }

  .nav {
    display: none;
  }

  .contenedor {
    font-size: .9em;
    position: relative;
  }

  .section1 {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .section2 {
    flex-direction: column;
    align-items: center;
    display: flex;
  }
}
